@import "src/common/styles/sass/variables";
.container {
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 8vh;
    width: 100%;
    background: $bgColor;
    z-index: 1;
    opacity: 0.9;
    .header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-wrap: nowrap;

    }
}
